import React, { useState } from 'react';
import './Notificacoes.css';

interface Notificacao {
  id: number;
  tipo: 'reserva' | 'alteracao' | 'cancelamento' | 'lembrete';
  mensagem: string;
  tempo: string;
}

const Notificacoes = () => {
  const [notificacoes, setNotificacoes] = useState<Notificacao[]>([
    {
      id: 1,
      tipo: 'reserva',
      mensagem: 'Sua reserva foi feita com sucesso!',
      tempo: '10 minutos atrás',
    },
    {
      id: 2,
      tipo: 'alteracao',
      mensagem: 'O horário da sua reserva foi alterado para as 16h.',
      tempo: '1 hora atrás',
    },
    {
      id: 3,
      tipo: 'cancelamento',
      mensagem: 'Sua reserva foi cancelada.',
      tempo: '2 horas atrás',
    },
    {
      id: 4,
      tipo: 'lembrete',
      mensagem: 'Lembrete: Sua reserva começa em 30 minutos.',
      tempo: '5 minutos atrás',
    },
  ]);

  const renderNotificacaoTipo = (tipo: string) => {
    switch (tipo) {
      case 'reserva':
        return 'Reserva Confirmada';
      case 'alteracao':
        return 'Alteração de Reserva';
      case 'cancelamento':
        return 'Cancelamento';
      case 'lembrete':
        return 'Lembrete';
      default:
        return '';
    }
  };

  return (
    <div className="notificacoes-container">
      <h1 className="titulo">Notificações e Lembretes</h1>
      <div className="notificacao-lista">
        {notificacoes.length === 0 ? (
          <p className="mensagem-vazia">Sem novas notificações</p>
        ) : (
          notificacoes.map((notificacao) => (
            <div
              key={notificacao.id}
              className={`notificacao-item ${notificacao.tipo}`}
            >
              <p className="tipo">{renderNotificacaoTipo(notificacao.tipo)}</p>
              <p className="mensagem">{notificacao.mensagem}</p>
              <span className="tempo">{notificacao.tempo}</span>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default Notificacoes;
